'use client'

import { useState } from 'react'
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Textarea } from "@/components/ui/textarea"
import { Separator } from "@/components/ui/separator"
import Link from "next/link"

interface HistoryItem {
  id: string
  prompt: string
  audioUrl: string
  timestamp: Date
}

export default function MartyApp() {
  const [prompt, setPrompt] = useState('')
  const [loading, setLoading] = useState(false)
  const [currentAudio, setCurrentAudio] = useState<string | null>(null)
  const [history, setHistory] = useState<HistoryItem[]>([])
  const [error, setError] = useState('')

  const handleGenerate = async () => {
    if (!prompt.trim()) {
      setError('Please enter some text to generate voice')
      return
    }

    setLoading(true)
    setError('')

    try {
      const response = await fetch('/api/generate', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ prompt: prompt.trim() }),
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'Failed to generate voice')
      }

      const newHistoryItem: HistoryItem = {
        id: Date.now().toString(),
        prompt: prompt.trim(),
        audioUrl: data.audioUrl,
        timestamp: new Date()
      }

      setCurrentAudio(data.audioUrl)
      setHistory(prev => [newHistoryItem, ...prev])
      setPrompt('')
    } catch (err) {
      setError('Failed to generate voice. Please try again.')
    } finally {
      setLoading(false)
    }
  }

  return (
    <main className="min-h-screen bg-black">
      {/* Navigation */}
      <nav className="border-b border-zinc-800 bg-zinc-900/50 backdrop-blur-xl fixed top-0 w-full z-50">
        <div className="container mx-auto px-4 py-4 flex items-center justify-between">
          <Link href="/" className="text-2xl font-bold text-white">
            Marty.ai
          </Link>
          <Button variant="ghost" className="text-gray-300 hover:text-white">
            Documentation
          </Button>
        </div>
      </nav>

      {/* Main Content */}
      <div className="container mx-auto px-4 pt-24">
        <div className="grid md:grid-cols-[1fr,300px] gap-6">
          {/* Left Column - Input and Audio Player */}
          <div className="space-y-6">
            <Card className="p-6 bg-zinc-900 border-zinc-800">
              <h2 className="text-xl font-bold text-white mb-4">Generate Voice</h2>
              <div className="space-y-4">
                <Textarea
                  placeholder="Enter your text here to generate voice..."
                  value={prompt}
                  onChange={(e) => setPrompt(e.target.value)}
                  className="min-h-[200px] bg-zinc-800 border-zinc-700 text-white"
                />
                {error && (
                  <p className="text-red-500 text-sm">{error}</p>
                )}
                <Button 
                  onClick={handleGenerate} 
                  disabled={loading || !prompt.trim()}
                  className="w-full bg-blue-600 hover:bg-blue-700 text-white relative"
                >
                  {loading ? (
                    <>
                      <span className="animate-pulse">Generating...</span>
                      <div className="absolute inset-0 bg-blue-600 opacity-50 rounded flex items-center justify-center">
                        <div className="w-6 h-6 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                      </div>
                    </>
                  ) : (
                    'Generate Voice'
                  )}
                </Button>
              </div>
            </Card>

            {currentAudio && (
              <Card className="p-6 bg-zinc-900 border-zinc-800">
                <h2 className="text-xl font-bold text-white mb-4">Generated Audio</h2>
                <div className="bg-zinc-800 rounded-lg p-4">
                  <audio controls className="w-full">
                    <source src={currentAudio} type="audio/mpeg" />
                    Your browser does not support the audio element.
                  </audio>
                </div>
                <div className="mt-4 flex justify-end">
                  <Button variant="outline" className="text-white border-zinc-700">
                    Download Audio
                  </Button>
                </div>
              </Card>
            )}
          </div>

          {/* Right Column - History */}
          <Card className="p-6 bg-zinc-900 border-zinc-800 h-fit">
            <h2 className="text-xl font-bold text-white mb-4">History</h2>
            <div className="space-y-4">
              {history.length === 0 ? (
                <p className="text-gray-400 text-center py-8">
                  No generations yet. Start by creating your first voice!
                </p>
              ) : (
                history.map((item) => (
                  <div 
                    key={item.id}
                    className="p-3 bg-zinc-800 rounded-lg cursor-pointer hover:bg-zinc-700 transition-colors"
                    onClick={() => setCurrentAudio(item.audioUrl)}
                  >
                    <p className="text-sm text-white line-clamp-2">{item.prompt}</p>
                    <p className="text-xs text-gray-400 mt-1">
                      {item.timestamp.toLocaleString()}
                    </p>
                  </div>
                ))
              )}
            </div>
          </Card>
        </div>
      </div>
    </main>
  )
}
